import React from 'react';
import { IconProps } from './interface';
import classNames from '../utils/classnames';

const Icon: React.FC<IconProps> = (props) => {
  const { type, name, size, className, spin, ...otherProps } = props;
  const svg = (type || name || '')
    .split('/')
    .filter((p: string) => !!p)
    .pop();
  return (
    <svg
      width="1em"
      height="1em"
      fill="currentcolor"
      className={classNames(className, {
        'zen-ui-icon-anticon-spin': !!spin || svg === 'LoadingOutlined',
      })}
      {...otherProps}
    >
      <use xlinkHref={`#${svg}`} />
    </svg>
  );
};
export default Icon;
